body {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: #e5e5e5;
}

main {
  background-color: #2e2f3e;
}

.row {
  display: flex;
}

#display {
  height: 120px;
  display: flex;
  justify-content: end;
  align-items: flex-end;
  padding: 0 10px;
  background-color: #242530;
}

#display p {
  margin: 0;
  font-size: 2em;
  color: white;
  font-weight: 500;
}

button {
  padding: 10px;
  width: 70px;
  height: 70px;
  border-radius: 0;
  border: 1px solid #262733;
  background-color: #2e2f3e;
  color: white;
}

#equals {
  background-color: #ffbb00;
}

#zero {
  width: 140px;
}

#clear {
  width: 210px;
  background-color: rgb(145, 22, 46);
}
